.filter{
    display: flex;
    flex-direction: row;
}

.input-text{
    background-color: white;
    width: 100%;
    border-radius: 10px;
    height: 2rem;
    padding: 0 15px;
    box-shadow: 0px 0px 8px #ddd;
    display: flex;
    align-items: center;
}

input {
    background-color: transparent;
    border: none;
    height: 100%;
    font-size: 1.25rem;
    width: 100%;
    margin-left: 5px;
}

input:focus {
    outline: none;
}

.options{
    border-radius: 0.5rem;
}

.order{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px;
    
}